@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&family=Montserrat:wght@500&family=Lexend+Mega&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;width:100%;height:90vh;display:flex;align-items:center;justify-content:center}.container{width:300px;height:350px;background-color:#000;display:flex;flex-direction:column;padding:3px;grid-gap:3px;gap:3px;font-family:Lexend Mega}.container .display-div{display:flex;flex-direction:column;flex:1 1;background-color:#fff;overflow:hidden}.container .display-div #display-history{flex:1 1;display:flex;height:auto;max-width:1fr;justify-content:flex-end;align-items:center;padding-right:5px;background-color:#fff;color:orange}.container .display-div #display{flex:1 1;display:flex;justify-content:flex-end;align-items:center;padding-right:5px;background-color:#fff}.container .input-div{flex:5 1;display:grid;grid-template-column:repeat(4, 1fr);grid-template-row:repeat(5, 1fr);grid-template-areas:"key-ac key-ac  key-div key-mult" "key-7  key-8   key-9   key-sub" "key-4  key-5   key-6   key-add" "key-1  key-2   key-3   key-calc" "key-0  key-0   key-dec key-calc";grid-gap:1px;gap:1px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.container .input-div .key{display:flex;align-items:center;justify-content:center;color:#fff;opacity:.95}.container .input-div .key:hover{opacity:1}.container .input-div .key:active{transform:scale(0.95)}.container .input-div .clear-key{background-color:orange}.container .input-div .calc-key{background-color:orange}.container .input-div .num-key{background-color:#fff;color:#000}.container .input-div .op-key{background-color:#d3d3d3;color:#000}.container .input-div #zero{grid-area:key-0}.container .input-div #one{grid-area:key-1}.container .input-div #two{grid-area:key-2}.container .input-div #three{grid-area:key-3}.container .input-div #four{grid-area:key-4}.container .input-div #five{grid-area:key-5}.container .input-div #six{grid-area:key-6}.container .input-div #seven{grid-area:key-7}.container .input-div #eight{grid-area:key-8}.container .input-div #nine{grid-area:key-9}.container .input-div #equals{grid-area:key-calc}.container .input-div #add{grid-area:key-add}.container .input-div #subtract{grid-area:key-sub}.container .input-div #multiply{grid-area:key-mult}.container .input-div #divide{grid-area:key-div}.container .input-div #decimal{grid-area:key-dec}.container .input-div #clear{grid-area:key-ac}.credit{margin-top:10px;text-align:center;font-family:Montserrat;font-size:10px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.credit a:link,.credit a:visited{color:#000}
